import React from "react";
import Typist from "react-typist";

import Particles from "react-particles-js";
import particleConfig from '../utils/particleConfig';

const TeleHero = props => (
  <Particles
    params={{
      particles: particleConfi
    }}
    style={{
      width: "100%"
    }}
  >
    <a
      class="link dim black b f2 f-headline-ns tc db mb3 mb4-ns monoton"
      href="#"
      title="Home"
    >
      TELESOUND
    </a>
    <Typist cursor={{ show: false }}>
      <header class="tc ph4">
        <div class="f3 f3-m f3-l fw2 black-80 mt0 lh-copy marker">
          Est. 1979
        </div>
        <div class="f4 f4-m f4-l fw2 black-80 mt0 lh-copy marker">
          TV, Electronics Repair
        </div>
      </header>
    </Typist>
    <div>5602, 17711 Vanowen St, Reseda, CA 91335</div>
  </Particles>
);

export default TeleHero;



